<template>
    <div class='contact'>
        <p>
            <span style=";font-family:华文中宋;font-size:20px">
                <span style="font-family:华文中宋">公司：</span>
            </span>
            <span style=";font-family:华文中宋;font-size:20px">
                <span style="font-family:华文中宋">上海斗文计算机系统集成工程有限公司</span>
            </span>
        </p>
        <p>
            <span style=";font-family:华文中宋;font-size:20px">
                <span style="font-family:华文中宋">地址：上海市</span>
            </span>
            <span style=";font-family:华文中宋;color:rgb(51,51,51);font-size:20px;">
                <span style="font-family:华文中宋">南京西路</span>1717号会德丰国际广场</span>
            <span style=";font-family:华文中宋;color:rgb(51,51,51);font-size:20px;">56楼5605室</span>
        </p>
        <p>
            <span style=";font-family:华文中宋;color:rgb(51,51,51);font-size:20px;">
                <span style="font-family:华文中宋">网站：</span>www.dataware-system.com</span>
        </p>
        <p>
            <span style=";font-family:华文中宋;color:rgb(51,51,51);font-size:20px;">
                <span style="font-family:华文中宋">邮箱：</span>qbm@dataware-system.com</span>
        </p>
        <p>
            <span style=";font-family:华文中宋;color:rgb(51,51,51);font-size:20px;">
                <span style="font-family:华文中宋">电话：</span>021-31185118</span>
        </p>
        <p>
            <span style=";font-family:华文中宋;color:rgb(51,51,51);font-size:20px;">
                <span style="font-family:华文中宋">传真：</span>021-31185119</span>
        </p>
        <p>
            <br/>
        </p>

        <div style="width:700px;height:550px;border:#ccc solid 1px;font-size:12px" id="map"></div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            map: null
        };
    },
    created() {
        this.initMap();
    },
    methods: {
        initMap() {
            this.createMap();//创建地图
            this.setMapEvent();//设置地图事件
            this.addMapControl();//向地图添加控件
            this.addMapOverlay();//向地图添加覆盖物
        },
        createMap() {
            this.map = new BMap.Map("map");
            this.map.centerAndZoom(new BMap.Point(121.451343, 31.228143), 18);
        },
        setMapEvent() {
            this.map.enableScrollWheelZoom();
            this.map.enableKeyboard();
            this.map.enableDragging();
            this.map.enableDoubleClickZoom()
        },
        addClickHandler(target, window) {
            target.addEventListener("click", function() {
                target.openInfoWindow(window);
            });
        },
        addMapOverlay() {
            var markers = [
                { content: "上海斗文系统是一家专业的系统集成商，致力于计算机、网络产品及智能化产品销售服务及系统集成工作", title: "上海斗文计算机系统集成工程有限公司", imageOffset: { width: 0, height: 3 }, position: { lat: 31.228425, lng: 121.451339 } }
            ];
            for (var index = 0; index < markers.length; index++) {
                var point = new BMap.Point(markers[index].position.lng, markers[index].position.lat);
                var marker = new BMap.Marker(point, {
                    icon: new BMap.Icon("http://api.map.baidu.com/lbsapi/createmap/images/icon.png", new BMap.Size(20, 25), {
                        imageOffset: new BMap.Size(markers[index].imageOffset.width, markers[index].imageOffset.height)
                    })
                });
                var label = new BMap.Label(markers[index].title, { offset: new BMap.Size(25, 5) });
                var opts = {
                    width: 200,
                    title: markers[index].title,
                    enableMessage: false
                };
                var infoWindow = new BMap.InfoWindow(markers[index].content, opts);
                marker.setLabel(label);
                addClickHandler(marker, infoWindow);
                this.map.addOverlay(marker);
            };
        },
        //向地图添加控件
        addMapControl() {
            var scaleControl = new BMap.ScaleControl({ anchor: BMAP_ANCHOR_BOTTOM_LEFT });
            scaleControl.setUnit(BMAP_UNIT_IMPERIAL);
            this.map.addControl(scaleControl);
            var navControl = new BMap.NavigationControl({ anchor: BMAP_ANCHOR_TOP_LEFT, type: BMAP_NAVIGATION_CONTROL_LARGE });
            this.map.addControl(navControl);
            var overviewControl = new BMap.OverviewMapControl({ anchor: BMAP_ANCHOR_BOTTOM_RIGHT, isOpen: true });
            this.map.addControl(overviewControl);
        }
    }
}
</script>

<style>
.contact {
    background-color: #e5e5e5;
    width: 1000px;
    margin: auto;
    padding-top: 20px;
    height: 600px;
}

.contact p {
    margin-left: 100px;
}
</style>




